<!-- 确定弹窗 -->
<template>
<div class="m-dialog" v-show="show">
	<div class="ctn">
		<slot name="dialog-header">
			<div class="head">
				<button type="button" class="close" @click="show=false"><span>×</span></button>
			</div>
		</slot>
		<slot name="dialog-body">
			<p>{{msg}}</p>
		</slot>
		<slot name="dialog-footer">
			<div class="btn btn-primary" @click="show=false">确认</div>
		</slot>	
	</div>
</div>
</template>

<script>
module.exports = {
	data: function () {
		return {
			msg: '',		//提示信息
			show: false,	
			callback: null,	//确认后的回调
		};
	},
	watch: {
		show: function (newVal){
			if( !newVal ){
				typeof this.callback === 'function' && this.callback();
				this.callback = null;
			}
		}
	},
};
</script>


<style>
.m-dialog{ position: fixed; left: 0; top: 0; z-index: 9999; width: 100%; height: 100%; overflow-x:hidden; overflow-y:scroll; text-align: center; background-color: rgba(0,0,0,.5); }
.m-dialog .ctn{ position: absolute; left: 50%; top: 50%; width: 230px; padding: 5px 10px; transform: translate(-50%, -50%); background: #fff; }
.m-dialog .btn{ margin: 20px 0; }
.m-dialog .ctn .head{ height: 30px; }
</style>


