<!-- 主文件 -->
<template>
	<div id="wrapper" :class="{ 'a-hideNav': bHideNav }">
		
		<!-- 底部导航 -->
		<app-header v-ref:header></app-header>

		<!-- 路由 -->
		<div id="router">
			<router-view  class="animated" transition="slide"></router-view>
		</div>

		<!-- 底部导航 -->
		<app-nav v-ref:nav></app-nav>


		<!-- 全局loading -->
		<loading v-ref:loading></loading>

		<!-- 全局alert -->
		<alert class="m-alert"
		    :show.sync="showAlert"
		    :duration="alertData.duration || 1500"
		    :type="alertData.type"
		    width="400px"
		    placement="top"
		    dismissable="true">
		    <span class="icon-info-circled alert-icon-float-left"></span>
		    <strong>{{alertData.text}}</strong>
		</alert>
	</div>
	
</template>


<script>
'use strict'; 
import store from '../vuex/store.js'
import appHeader from './header.vue'
import appNav from './nav.vue'
import {alert} from '../common/js/vue-strap'

module.exports = {
	data: function () {
		return {
			bHideNav: false,	//隐藏侧边导航
			showAlert: false,
		}
	},
	vuex: {
		getters: {
			alertData: state => state.common.alertData || {},
			show: state => state.common.showAlert || false,
		},
	},
	watch: {
		show: function (newVal) {
			if( newVal )this.showAlert = true;
		},
	},
	components: {
		appHeader,
		appNav,
		alert,
	},
	store: store
}


</script>

<style> 
@import '../common/css/sass.scss';
#router{ position: relative; width: $max-width; margin: 0 auto; overflow-x: _hidden; background-color: #ffffff;  padding-top: px2rem(80px); }
</style>



