<!--  -->
<template>
<div class="m-nav">
	<ul>
		<li class="item" :class="{ active: curPath===item.path }" v-for="item in routers"><a href="javascript:void(0);" @click="$router.go(item.path)">{{item.name}}</a></li>
	</ul>
</div>
</template>

<script>
import router from '../router/router.config.js'
import Vue from 'vue'

export default {
	data () {
		return {
			bShow: true,
            bShow2: false,
			bShow3: false,
			bShow4: false,

			//path: 跳转路径，   activeRoute: 高亮路径
			routers: [ 
				{ path: '/schedule', name: '课程表', activeRoute: ['/schedule']},
				{ path: '/contact', name: '联系', activeRoute: ['/contact']},
                { path: '/name_list', name: '花名册',activeRoute: ['/name_list']},
				{ path: '/news', name: '动态', activeRoute: ['/news']},
				{ path: '/me', name: '我', activeRoute: ['/me']},
            ],
            curPath: '',
		}
	},
	methods: {
		go (path) {
			this.$router.go(path);
		}
	},
	computed: {
		curPath (){
			return this.$route.matched[0].handler.fullPath
		}
	},
	activate (done) {
		var $vm = this;
		this.$router.afterEach(transition => {
			$vm.path = transition.to.path;
		});
		done();
	}
};

</script>

<style >
@import '../common/css/sass.scss';
.m-nav{ position: fixed; z-index: 999; left: 0; bottom: 0; right: 0; margin:0 auto; width: 100%; max-width: $max-width; height: px2rem(116px); background-color: #f8f8f8; text-align: center; border-top: 1px solid #b8b8b8; }
.m-nav li{ float: left; width: 20%; }
.m-nav li a{ display: block; padding-top: px2rem(80px); line-height: px2rem(36px); font-size: px2rem(24px); color:#8f8f8f; }
.m-nav li.active a{ color: #09bb07; }
</style>


