var Vue = require('vue');
var VueRouter = require('vue-router');
Vue.use(VueRouter);

var router = new VueRouter({
	hashbang: false
});

// 定义路由规则
router.map({

    // '/test': {
    //     component: function (resolve, reject) {
    //         require(['../views/test'], resolve);
    //     }
    // },

	// /user/:username/post/:post_id
	//首页
	'/schedule': {
		component: function (resolve, reject) {
            require(['../views/schedule'], resolve);
        }
	},

    // 联系
    '/contact': {
        component: function (resolve, reject) {
            require(['../views/contact'], resolve);
        }
    },

    // 花名册
    '/name_list': {
        component: function (resolve, reject) {
            require(['../views/name_list'], resolve);
        }
    },

    // 动态
    '/news': {
        component: function (resolve, reject) {
            require(['../views/news'], resolve);
        }
    },

    // 我
    '/me': {
        component: function (resolve, reject) {
            require(['../views/me'], resolve);
        }
    },
});
// 重定向
router.redirect({
	'*': '/me'
});

module.exports = router;


