/* 公共信息 */
import {
  COMMON__SHOW_ALERT,
  COMMON__GET_BASE_DATA,
} from '../mutation-types'

// initial state
// shape: [{ id, quantity }]
const state = {
  showAlert: false,
  alertData: {},
  baseData: {},
}


const mutations = {
  
  // alert提示信息
  [COMMON__SHOW_ALERT] (state,  data = { type: 'success', text: 'tips', duration: 1500 }) {
    state.showAlert = true;
    setTimeout(function () {
    	state.showAlert = false;
    }, 500);
    state.alertData = data;
  },

  // 
  [COMMON__GET_BASE_DATA] (state, baseData) {
    state.baseData = baseData;
  }

}


export default {
  state,
  mutations,
}



