import './common/js/flexible.js'
import '../node_modules/normalize.css/normalize.css'
import '../node_modules/animate.css/animate.min.css'
import './common/css/common.css'
import $ from 'jquery'
window.$ = window.jQuery = $;
let Vue = require('vue'); 
let Api = require('./service/api.js');
import FastClick from 'fastclick'

FastClick.attach(document.body);

import Sortable from './plugins/sortable.js'
Vue.use( Sortable );


import Tips from './plugins/tips.js'
Vue.use( Tips );

//Vue.$$全局变量 --start
//获取共公信息
Vue.$$getBaseData = (function (){
	var baseData = null;
	return function (){
		if( baseData ){
			return baseData;
		}
		$.ajax({
			url: Api.get('getBaseData'),
			type: 'GET',
			dataType: 'json',
			async: false
		})
		.done(function(res) {
			if( res.code != 200 )return;
			baseData = res.data;
		});
		return baseData;
	}
})();
//Vue.$$全局变量 --start


//非正式线上环境,开启debug模式
if( Api.env!=='publish' ){
    Vue.config.debug = true;
}

// 路由配置
var router = require('./router/router.config.js');		
var App = require('./components/App.vue');	

//ajax配置
require('./service/ajax.config.js');		

// 注册全局全组
Vue.component('loading', require('./components/loading.vue'));
Vue.component('alert', require('./components/alert.vue'));

/**
 * 确认弹窗
 * @param  {[type]}   msg [提示信息]
 * @param  {Function} fn  [点解确认按钮后的回调]
 * @return {[type]}       [description]
 */
(function (){
	var $alert = null;
	window._alert = window.alert;
	window.alert = function (msg, fn){
		$alert = $alert || Vue.$$app.$refs.alert;
		$alert.msg = msg;
		$alert.show = true;
		$alert.callback = fn;
	}
})();


//路由切换动画
Vue.transition('bounce', {
  enterClass: 'bounceInRight',
  leaveClass: 'bounceOutLeft'
});
Vue.transition('slide', {
  enterClass: 'slideInRight',
  leaveClass: 'slideOutLeft'
});

import { sync } from 'vuex-router-sync';
import store from './vuex/store.js'
sync(store, router);

//开启路由，生成根实例（路由器会创建一个 App 实例，并且挂载到选择符 #app 匹配的元素上。）
router.start(App, '#app', function () {
	//资源加载完成,取消loading
	var oLoading = document.getElementById('loading');
	oLoading.getElementsByClassName('ctn')[0].style.opacity = '0';
	// oLoading.setAttribute('class', 'animated fadeOut');
	setTimeout(function () {
		oLoading.remove();
	},1000);

	Vue.$$app = router.app;	//根实例

});

window.Vue = Vue;


