/* 公共信息 */
import {
  GET_BASE_DATA,
} from '../mutation-types'

// initial state
// shape: [{ id, quantity }]
const state = {
  data: {},
}

// mutations
const mutations = {
  [GET_BASE_DATA] (state, baseData) {
    state.data = baseData;
  }
}

export default {
  state,
  mutations
}



