(function() {

  var vSortable = {}
  var Sortable = typeof require === 'function' ? require('sortablejs') : window.Sortable

  if (!Sortable) {
    throw new Error('[vue-sortable] cannot locate Sortable.js.')
  }

  // exposed global options
  vSortable.config = {}

  vSortable.install = function(Vue) {
    Vue.directive('sortable', {
      update (options) {
          options = options || {};
          var self = this;
          
          // var sortable = new Sortable(this.el, options)
            Sortable.create(this.el, {
                animation: 150,
                // handle: '.uk-nestable-handle',
                onEnd: function (e) {
                    // Remove the dragged item from dom
                    e.item.remove()
                    deb
                    // Clone container
                    var clone = _a.clone(self._scope.model[self._scope.$key])
                    self._scope.model[self._scope.$key] = []

                    // Move to new position
                    var swappedEl = clone.splice(e.oldIndex, 1)[0]
                    clone.splice(e.newIndex, 0, swappedEl)

                    // On next Tick update with clone
                    self._scope.$nextTick(function() {
                        self._scope.model[self._scope.$key] = clone
                    })
                }
            })

          if (this.arg && !this.vm.sortable) {
            this.vm.sortable = {}
          }

          //  Throw an error if the given ID is not unique
          if (this.arg && this.vm.sortable[this.arg]) {
            console.warn('[vue-sortable] cannot set already defined sortable id: \'' + this.arg + '\'')
          } else if (this.arg) {
            this.vm.sortable[this.arg] = sortable
          }
        }
    })


    
  }



  if (typeof exports == "object") {
    module.exports = vSortable
  } else if (typeof define == "function" && define.amd) {
    define([], function() {
      return vSortable
    })
  } else if (window.Vue) {
    window.vSortable = vSortable
    Vue.use(vSortable)
  }

})()


