/* 公共信息 */
import {
  SORT_LIST_MANAGE__GET_LIST,
} from '../mutation-types'

// initial state
// shape: [{ id, quantity }]
const state = {
  sortList: {},
}

// mutations
const mutations = {
  [SORT_LIST_MANAGE__GET_LIST] (state, data) {
    state.sortList = data;
  },

}

export default {
  state,
  mutations
}



