/*
	Tips提示信息
	usage:   Vue.install(Tips);
	Vue.tips('This is a tips', 1500);
 */
;(function() {
  var Tips = {};

  Tips.install = function(Vue) {
  	var dom = document.createElement('div');
  	dom.style.display = 'none';
  	dom.style.zIndex = '9999';
  	dom.style.maxWidth = '80%';
  	dom.style.lineHeight = '32px';
  	dom.style.padding = '.1rem';
  	dom.style.color = '#fff';
  	dom.style.background = 'rgba(0,0,0,.5)';
  	dom.style.position = 'fixed';
  	dom.style.left = '50%';
  	dom.style.top = '50%';
  	dom.style.transform = 'translate(-50%, -50%)';

  	document.body.appendChild(dom);
	Vue.tips = function (tips, duration){
		

		dom.innerHTML = tips;
		dom.style.display = 'block';
	}
  }

  if (typeof exports == "object") {
    module.exports = Tips
  } else if (typeof define == "function" && define.amd) {
    define([], function() {
      return Tips
    })
  } else if (window.Vue) {
    window.Tips = Tips
    Vue.use(Tips)
  }
})();


