var Vue = require('vue');
var Api = require('./api.js');
var bFinished = false;

//在每个请求之前被发送和$.ajax()处理它们前处理
$.ajaxPrefilter( function( options, originalOptions, jqXHR ) {
    //如果请求接口不是http的，测使用mock数据
    if( !/^http/.test(Api.getRoot()) ){
        // var o = require(originalOptions.url);
        require('./mock_api/'+originalOptions.url);
    }

    
});

// 设置ajax默认值
$.ajaxSetup({

    // 请求前出loading
    beforeSend: function() {
        bFinished = false;
        setTimeout(function () {
            //对于请求快于100毫秒的,就不出loading
            if(!bFinished){
                Vue.$$app.$root.$refs.loading.show = true;
            }
        }, 100);
    },
    dataFilter: function (response) {
        bFinished = true;
        //对于请求快于100毫秒的,就不出loading
        Vue.$$app.$root.$refs.loading.show = false;

        var data = JSON.parse(response);
        var msg = data.msg;
        switch( data.code ){
            case 200:
                return JSON.stringify(data);
                break;
            case 401:
                
                //生产和测试环境跳登录页
                if( Api.env === 'publish' || Api.env === 'test' ){
                    location.href="http://dap-admin.vip.vip.com/integration.php?c=home&a=login&backUrl="+location.href;
                }else{
                    // alert('您没有操作权限哦！');
                }
                return JSON.stringify(data);
                break;
            case 400:
                alert(msg);
                return JSON.stringify(data);
                break;
            case 404:
                alert(msg);
                return JSON.stringify(data);
                break;
            case 406:
                alert(msg);
                return JSON.stringify(data);
                break;
            case 407:
                alert('您没有操作权限哦！');
                return JSON.stringify(data);
                break;
            case 500:
                alert(msg);
                return JSON.stringify(data);
                break;
            case 1000:
                alert(msg);
                return JSON.stringify(data);
                break;
            case 5000:
                alert(msg);
                return JSON.stringify(data);
                break;
            default:
                alert(msg);
                return JSON.stringify(data);
                break;
        }

    },
    
    // 错误统一处理,可以覆盖
    error: function (res){
        alert('网络异常，请稍后再试');
    },
});


//ajax fail异常处理, 这个不会被.fail(fn)覆盖
$(document).ajaxError(function() {
    //150延迟，是因为beforeSend延迟了100
    setTimeout(function () {
        Vue.$$app.$root.$refs.loading.show = false;
    }, 150);
});

